import React from "react";

import Skill from "./Skill";
import HomePageProjects from "./HomePageProjects";

import HomePageBlogs from "./HomePageBlogs";
import ContactMe from "./ContactMe";
import Footer from "./Footer";
import Aboutsec from "./AboutSection";
// Wrap each individual component with React.memo
const MemoizedAboutsec = React.memo(Aboutsec);
const MemoizedSkill = React.memo(Skill);
const MemoizedHomePageProjects = React.memo(HomePageProjects);
const MemoizedHomePageBlogs = React.memo(HomePageBlogs);
const MemoizedContactMe = React.memo(ContactMe);
const MemoizedFooter = React.memo(Footer);

const HomePage = ({ isDarkMode }) => {
  return (
    <div>
      <div style={{ marginTop: "-70px" }}>
        <MemoizedAboutsec isDarkMode={isDarkMode} />
      </div>
      <div style={{ marginTop: "-50px" }}>
        <MemoizedSkill />
      </div>

      <MemoizedHomePageProjects />
      <MemoizedHomePageBlogs />
      <div style={{ marginTop: "-30px" }}>
        {" "}
        <MemoizedContactMe />
      </div>

      <MemoizedFooter />
    </div>
  );
};

export default HomePage;
